
<template>
   <div class="rechargeRecord">
      <div v-html="content"></div>
       <!-- <p></p>
       <p>1.</p>
       <p></p>
       <p>2.</p>
       <p></p>
       <p>3.</p>
       <p></p>
       <p>4.</p>
       <p></p>
       <p>5.</p>
       <p></p> -->
   </div>
</template>

<script>
    import api from '../network/request'
    export default {
        name: "rechargeRecord",
        components: {
        },

        data() {
            return {
                content:''
            }
        },
        methods: {
          rule:function(){
            var _this=this;
            const params={access_token:localStorage.getItem('access_token')};
            api.post('recharge/recharge_rule',params,res=>{
            console.log(res);
                if(res.status==0){
                  _this.content=res.data.content
                  // _this.status=res.data;
                }
                if(res.status==600){
                    this.$router.push({name: 'login', params: {}})
                }
            })
          }
           
        },
        created() {
          this.rule()
        }
    }
</script>

<style scoped>
	.rechargeRecord{
		position: fixed;
		height: 100%;
		width: 100%;
		background-color: #fff;
		padding: 15px;
		color: #000000;
		font-size: 14px;
	}
	.rechargeRecord p{
		line-height: 24px;
	}
</style>



